@import '../../config.css';

.loginPane {
    position: fixed;
    height: 100vh;
    bottom: 0px;
    z-index: 4;
    width: 100%;
    background-image: url('../header/images/img7.png');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 60% 50%;
    background-size: cover;
}

.loginPane::before {
    content: '';
    background: inherit; 
    position: absolute;
    left: -2px;
    right: 0px;
    top: -2px; 
    bottom: 0;
    -webkit-box-shadow: inset 0 0 0 3000px rgba(255,255,255,0.3);
            box-shadow: inset 0 0 0 3000px rgba(255,255,255,0.3);
    -webkit-filter: blur(5px);
            filter: blur(5px);
    -webkit-transition: -webkit-filter 1s;
    transition: -webkit-filter 1s;
    -o-transition: filter 1s;
    transition: filter 1s;
    transition: filter 1s, -webkit-filter 1s;
}

.loginPane > * {
    display: inline-block;
    -webkit-transition: display 1s;
    -o-transition: display 1s;
    transition: display 1s;
}

.loginPane.fadeOut::before {
    -webkit-filter: blur(0px);
            filter: blur(0px);
}

.loginPane.fadeOut > * {
    display: none;
}

.form {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: var(--handwritingFont);
    text-align: center;
    width: 100%;    
    -webkit-transition:  left 0.5s;    
    -o-transition:  left 0.5s;    
    transition:  left 0.5s;
}

.cat {
    position: absolute;
    top: 50%;
    left: 150%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-transition:  left 0.5s;
    -o-transition:  left 0.5s;
    transition:  left 0.5s;
}
.cat.loginAttempt {
    left: 45%;
}
.form.loginAttempt {
    left: -100%;
}

.perc {
    text-align: center;
    font-family: var(--handwritingFont);
    font-size: 1.8em;
    padding-left: 25%;
}

.input {
    display: inline-block;
    position: relative;
}

.input > input  {
    width: 100px;
    background-color: transparent;
    border: none;
    padding: 10px;
    font-size: 20px;
    margin-bottom: 50px;
    text-align: center;
    outline: none;
    border-radius: 0px;
}

.input > span {
    background-color: grey;
    display: inline-block;
    position: absolute;
    width: 120px;
    height: 1px;
    right: 0px;
    top: 42px;
    -webkit-transition: background-color 1s;
    -o-transition: background-color 1s;
    transition: background-color 1s;
}

.securityCode:focus + span , .password:focus + span {
    background-color: black;
}

.dash {
    display: inline;
    padding: 10px;
}

.prompt {
    font-size: 35px;
}

#loginBtn {    
    display: block;
    margin: auto;
    font-size: 0.8em;
}

 @media(min-width: 800px){
    .input > input {
        width: 200px;
    }
    .input > span {
        width: 220px;
    }
} 


