.textarea {
    padding: 15px;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.textarea > .input {
    font-family: var(--mainFont);
    font-size: 1.2em;
    width: 100%;
    display: block;
    outline: none;
    border: none;
    background-color: inherit;
    min-height: 170px;
    resize: none;
}

.post {
    position: absolute;
    bottom: 0px;
    right: 10px;
    display: block;
    width: 100px;
}


.inputComment {
    border: 1px solid #C1C1C1;
    border-radius: 3px;
    background-color: var(--offWhite);
    min-height: 0px; 
    position: relative;
    -webkit-box-shadow: 0px 0px 3px #C1C1C1;
            box-shadow: 0px 0px 3px #C1C1C1;
    -webkit-transition: min-height 0.2s;
    -o-transition: min-height 0.2s;
    transition: min-height 0.2s;
}

.inputComment.active {
    overflow: auto;
    min-height: 200px;
    padding-bottom: 50px;
    height: auto;
}

.inputComment > button {
    display: none;
}

.inputComment.active  button , .inputComment.active p {
    display: initial;
}

.inputComment p {
    margin: 0px;
    outline: none;
    -webkit-transition: opacity 0.2s, -webkit-transform 0.2s;
    transition: opacity 0.2s, -webkit-transform 0.2s;
    -o-transition: transform 0.2s, opacity 0.2s;
    transition: transform 0.2s, opacity 0.2s;
    transition: transform 0.2s, opacity 0.2s, -webkit-transform 0.2s;
}

.inputComment.active p {
    opacity: 0;
    -webkit-transform: translate(0px, -25px);
        -ms-transform: translate(0px, -25px);
            transform: translate(0px, -25px);
    position: absolute;
}  

.inputComment.active .emoji {
    display: initial;
}

.emoji {
    display: none;
    color: grey;
    position: absolute;
    bottom: 20px;
    left: 15px;
}

.emoji:hover {
    color: black;
}

.picker {
    display: none;
}

.show {
   display: initial;
   position: relative; 
   top: -300px;
}


